/* css for home, contact, and nav */
iframe {
  width: 75vw;
  height: 50vw;
  margin-left: 12.5%;
  margin-top: 15%;
  margin-bottom: 5vw;
  overflow: hidden;
}
#home-blurb {
  width: 90%;
  border: .1vw solid black;
  border-radius: .1vw;
  position: absolute;
  left: 5vw;
  padding: .4vw;
  background-color: white;
  font-size: 1.25vw;
}
#insta-feed {
  position: absolute;
  margin-left: 0;
  margin-bottom: 10%;
  left: 17.5%;
  height: 50vh;
  width: 70%;
}
body {
  font-family: "Roboto Mono", monospace;
  font-optical-sizing: auto;
  font-weight: 500;
  /*
  font-family: "Mukta", sans-serif;
  font-weight: 200;
  font-style: normal; 
  */
  background-color: #b9d2e0;
}
.nav {
    background-color: #2F70A6 ;
    overflow: hidden;
}
.nav-logo {
  width: 4%;
  height: auto;
  position: absolute;
  left: 48%;
  margin-top: -0.3vw;
}
 
.link_styles {
    float: left;
    color: #f2f2f2;
    margin-top: .2vw;
    text-align: center;
    padding: 0.9vw 1vw;
    text-decoration: none;
    font-size: 1.3vw;
    
    
  }
.link_styles:first-child {
    margin-left: 32.25%;
}
.special_link {
  margin-right: 4%;
}
  
.link_styles:hover {
    
    animation: hover 0.5s linear 1 forwards;

  }
@keyframes hover {
    from {background-color: #2F70A6;}
    to {background-color: #1f89d4;}
  }
  
  
#banner { 
    position: relative;
    background-repeat: no-repeat;
    border-top:  0.2vw solid #2F70A6;

}
.team_photo {
  object-fit: contain;
  filter: grayscale(0.8) blur(3.5px);
  width: 100%;

  
}
.logo {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(0deg);
  width: 15vw;
  height: 15vw;
  transform-origin: 40% 50%;
  filter: brightness(105%);
  transition: transform 1s linear;
}

.title {
  position: absolute;
  top: 60%;
  left: 47.5%;
  transform: translate(-50%, -50%);
  font-size: 3.5vw;
  color: #2F70A6;

  font-family: "Teachers", sans-serif;
  font-optical-sizing: auto;

  font-weight: 800;
}
.contact_card {
  
  width: 100vw;
  height: auto;
}
