#banner { 
    position: relative;
    background-repeat: no-repeat;
    border-top:  0.2vw solid #2F70A6;

}
.team_photo {
  object-fit: contain;
  filter: grayscale(0.8) blur(3.5px);
  width: 100%;

  
}
.team_title {
    position: absolute;
    top: 25%;
    left: 47.5%;
    transform: translate(-50%, -50%);
    font-size: 3.2vw;
    color: #2F70A6;
  
    font-family: "Teachers", sans-serif;
    font-optical-sizing: auto;
  
    font-weight: 800;
  }
  
.card {
    border: 0.2vw solid rgb(78, 75, 75);
    background-color: rgb(255, 255, 255);
    width: 24vw;
    height: 36vw;
    margin-left: 0.5vw;
    border-radius: 1vw;    
    text-align: center;
    font-size: 2vw;
}
.card_container {
    margin-top: 3%;
    margin-bottom: 3%;
    display: flex;
}
.card_img {
    width:18vw;
    border-radius: 100vw;
    margin-left: 3vw;
    margin-right: 3vw;
    margin-top: 1vw;
    height: 18vw;
    border: 0.2vw solid gray;
    object-fit: cover;
}
h1 {
    text-align: center;
    margin-left: 3.5%;
}